
.popmovies{
  display:grid;
  grid-template-columns: repeat(auto-fit,minmax(250px,1fr));
  grid-column-gap:1rem;
  grid-row-gap: 2rem;
}

img{
  width: 100%;
  height: 30vh;
  object-fit: cover;
}

.buttons button{
  margin-right: 2rem;
  min-width: 5rem;
  border: none;
  padding: 0.5rem 1rem;
  background-color: white;
  color: rgb(65, 98, 168);
  border-radius: 1rem;
  border: 2px solid rgb(65, 98, 168);
  font-weight: bold;
  cursor: pointer;
}

button.active{
  background-color: rgb(65, 98, 168);
  color: white;
}

